export default class Forms {
    constructor(forms) {
        this.forms = document.querySelectorAll(forms)
        this.message = {
            load: 'Загрузга...',
            success: 'Спасибо! Скоро мы с вами свяжемся!',
            error: 'Что то пошло не так'
        }
    }

    createMessage(message) {
        const createMessage = document.createElement('div')
        createMessage.style.cssText = `
            width: 200px;
            height: 100px;
            color: red;
        `

        this.forms.forEach(form => {
            form.parentNode.appendChild(createMessage)
        })

        createMessage.textContent = this.message[message]
        
        if(message === 'delete') {
            setTimeout(() => {
                createMessage.remove()
            }, 5000)
        }
    }

    async postData(url, data) {
        const response = await fetch(url, {
            method: 'POST',
            body: JSON.stringify(data),
        })

        return response
    }

    sendForm() {
        this.forms.forEach(form => {
            form.addEventListener('submit', (e) => {
                e.preventDefault()
                this.createMessage('load')

                const formData = new FormData(form)

                this.postData('/src/assets/question.php', formData)
                    .then(response => {
                        console.log(response)
                        this.createMessage('success')
                    })
                    .catch(error => {
                        this.createMessage('error')
                        console.log(error)
                    })
                    .finally(this.createMessage('delete'))
            })
        })
    }

    render() {
        this.sendForm()
    }
}